.listCharacters{
    padding:30px;
    display: grid;
    grid-template-columns: 320px 320px 320px 320px;
    grid-template-rows: 500px;
  
    @media screen and (max-width: 1100px) {
     display:flex;
     flex-wrap: wrap;
     justify-content: center;
     padding:0;

    }
    @media screen and (max-width: 320px) {
        grid-template-columns: 300px;
        padding:0;
      }
}